<template>
  <div style="font-family: 'Quicksand', sans-serif;">
    <div class="py-4 px-2 shadow bg-blue-light text-white font-bold">
      <p class="mt-1 font-semibold text-xs float-right mr-3">
        History Absen
      </p>
      <div @click="$router.go(-1)" class="ml-2 cursor-pointer">
          <svg  width="20" height="20" style="fill: white;" class="d-inline mr-1" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 320 512"><!--!Font Awesome Free 6.5.1 by @fontawesome - https://fontawesome.com License - https://fontawesome.com/license/free Copyright 2024 Fonticons, Inc.--><path d="M9.4 233.4c-12.5 12.5-12.5 32.8 0 45.3l192 192c12.5 12.5 32.8 12.5 45.3 0s12.5-32.8 0-45.3L77.3 256 246.6 86.6c12.5-12.5 12.5-32.8 0-45.3s-32.8-12.5-45.3 0l-192 192z"/></svg>
      </div>
    </div>
    <hr class="my-2">
    <div class="row justify-content-center">
        <div class="col-12 p-3 text-right">
            <button type="button" @click="sendData" class="btn btn-sm btn-outline-primary mr-4"><i class="fa-solid fa-rotate"></i> Sync data to server </button>
        </div>
        <div class="col-11 shadow rounded-lg" style="font-family: 'Quicksand', sans-serif;">
            <div v-for="(item, index) in $store.state.checktimes" :key="index" class="p-1">
                <div class="shadow rounded-lg p-3 row" :class="item.source_data!='local'?'':'bg-red-400'">
                    <div class="col-3">
                        <img v-viewer :src="item.dataURL" alt="">
                    </div>
                    <div class="col-9">
                        <p class="text-xs font-bold">{{item.name}}</p>
                        <p class="text-xs font-semibold">Checktime : {{formatTgl(new Date(item.checktime),'DD-MM-YYYY, HH:mm:ss')}}</p>
                        <p class="text-xs font-semibold">Location : {{item.type_location}}</p>
                        <p class="text-xs font-semibold">Koordinate : <a target="__blank" :href="`http://www.google.com/maps/place/${item.lat},${item.lng}`">{{item.lat}},{{item.lng}}</a></p>
                    </div>
                </div>
            </div>
        </div>
    </div>
  </div>
</template>
<script>
import axios from 'axios';
import autophp from '@/plugins/auto';
let sdb = new autophp('ci');
export default {
  data() {
    return {
    }
  },
  methods: {
    async sendData(){
        let that=this;
        sdb.loadingOn()
        await this.$store.dispatch('sync_absen')
        sdb.loadingOff();
        that.$store.dispatch('update_employees')
    },
   formatTgl(tgl, pattern = "YYYY-MM-DD") {
      return this.$datefns.format(new Date(tgl), pattern);
    },
  },
  mounted() {
    let that=this;
    window.checktimes.find({},function(err,docs){
        if(docs && docs.length>0){
            docs.sort((a, b) => {
                return new Date(b.checktime) - new Date(a.checktime);
            });
            that.$store.state.checktimes=docs;
            that.sendData()
        }
    })
  }
};
</script>